@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap);
:root {
    --font-family: 'Manrope', sans-serif;
    --gradient-text: linear-gradient(90deg, #38adff, #259fff, #2290fc, #3081f8, #4470f2, #585de8, #6b46dc, #7c27cc);
    --gradient-text2: linear-gradient(90deg, #ff5f38, #ff5360, #fc5384, #ea5ca2, #d06aba, #b076c9, #8e81cf, #6e88cc);
    --gradient-bar: linear-gradient(103.22deg, #AE67FA -13.86%, #F49867 99.55%);
    --color-bg: #040C18;
    --color-blog: #042c54;
    --color-text: #81AFDD;
    --color-subtext: #FF8A71;
    --color-button: #51c404;
    ;
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  scroll-behavior: smooth;
}

body {
  background: var(--color-bg);
}

a {
  color: unset;
  text-decoration: none;
}

.gradient__bg {

  /* safari 5.1+,chrome 10+ */

  /* opera 11.10+ */

  /* ie 10+ */

  /* global 92%+ browsers support */
  background:radial-gradient(circle at 3% 3%, rgba(0, 40, 83, 1) 0%, rgba(4, 12, 24, 1) 50%);
}

.gradient__text {
  background: var(--gradient-text);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.gradient__text2 {
    background: var(--gradient-text2);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.section__padding {
  padding: 4rem 25%;
}

.section__margin {
  margin: 4rem 6rem;
}

.scale-up-center {
	-webkit-animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
	animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}

@-webkit-keyframes scale-up-center {
  0% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes scale-up-center {
  0% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@media screen and (max-width: 2000px) {
  .section__padding {
    padding: 4rem 15%;
  }
  
  .section__margin {
    margin: 4rem 15%;
  }
}

@media screen and (max-width: 1600px) {
  .section__padding {
    padding: 4rem 10%;
  }
  
  .section__margin {
    margin: 4rem 10%;
  }
}

@media screen and (max-width: 550px) {
  .section__padding {
    padding: 4rem 2rem;
  }

  .section__margin {
    margin: 4rem 2rem;
  }  
}
.og__footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.og__footer-heading {
    width: 100%;
    text-align: center;

    margin-bottom: 3rem;
}

.og__footer-heading h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 64px;
    line-height: 75px;
}

.og__footer-copyright {
    margin-top: 2rem;
    text-align: center;
    width: 100%;
}

.og__footer-copyright p {
    /* font-size: 18px;
    font-family: var(--font-family);
    line-height: 15px;
    color: #fff; */
    font-family: var(--font-family);
    font-weight: 400;
    color: var(--color-text);
}

.og__footer-tweet {
    margin-bottom: 4rem;
    width: 600px;
    border-radius: 25px;
}

.og__footer-avatar {
    padding: 1rem;
    height: 150px;
    width: 150px;
    border-radius: 50px;
}

@media screen and (max-width: 1050px) {
    .og__footer-heading h1 {
        font-size: 44px;
        line-height: 50px;
    }
    .og__footer-tweet {
        margin-bottom: 4rem;
        width: 600px;
        border-radius: 25px;
    }
}

@media screen and (max-width: 650px) {
    .og__footer-heading h1 {
        font-size: 34px;
        line-height: 42px;
    }

    .og__footer-tweet {
        margin-bottom: 4rem;
        width: 400px;
        border-radius: 25px;
    }
}

@media screen and (max-width: 490px) {
    .og__footer-heading h1 {
        font-size: 27px;
        line-height: 38px;
    }
}

.og__header {
    display: flex;
    flex-direction: row;
}

.og__header-content {
    flex: 1 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    margin-right: 5rem;
}

.og__header-content img {
    flex: 1 1;
    max-width: 1000px;
}

.og__header-content h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 62px;
    line-height: 75px;
    letter-spacing: -0.04em;
}

.og__header-content h3 {
    color: var(--color-text)
}

.og__header-content p {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 20px;
    line-height: 28px;
    color: var(--color-text);
    margin-top: 1.5rem;
}

.og__header-content .smalltext {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 15px;
    line-height: 20px;
    margin-top: 0.5rem;
}

.og__header-content__input button {
    min-height: 50px;
    min-width: 150px;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 20px;
    line-height: 28px;
    background: var(--color-button);
    border: 2px solid var(--color-button);
    margin: 2px;
    padding: 0 1rem;
    color: #fff;
    cursor: pointer;
    outline: none;
    border-radius: 5px;
}

.og__header-content__input .plusminus {
    background: transparent;
    min-width: 10px;
}

@media screen and (max-width: 1050px) {
    .og__header {
        flex-direction: column;
    }

    .og__header-content img {
        flex: 1 1;
        max-width: 700px;
    }

    .og__header-content {
        margin: 0 0 3rem;
    }
}

@media screen and (max-width: 650px) {
    .og__header-content h1 {
        font-size: 48px;
        line-height: 60px;
    }

    .og__header-content img {
        flex: 1 1;
        max-width: 500px;
    }

    .og__header-content p {
        font-size: 16px;
        line-height: 24px;
    }

    .og__header-content__people {
        flex-direction: column;
    }

        .og__header-content__people p {
            margin: 0;
        }

    .og__header-content__input button {
        font-size: 16px;
        line-height: 24px;
    }
}

@media screen and (max-width: 490px) {
    .og__header-content h1 {
        font-size: 36px;
        line-height: 48px;
    }


    .og__header-content img {
        flex: 1 1;
        max-width: 250px;
    }

    .og__header-content p {
        font-size: 14px;
        line-height: 24px;
    }

    .og__header-content__input button {
        font-size: 12px;
        line-height: 16px;
    }
}

@font-face {
    font-family: 'logofont';
    src: local('logofont'), url(/MoonbirdsAtHome/static/media/logos.f0aee214.woff) format('woff');
  }

.og__navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 4rem 25%;
}

.og__navbar-links {
    flex: 1 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.og__navbar h3 {
    color: var(--color-text);
}

.og__navbar-links_logo {
    margin-right: 2rem;
}

.og__navbar-links_container {
    display: flex;
    flex-direction: row;
}

.og__navbar-sign {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.og__navbar-links_container a,
.og__navbar-sign a,
.og__navbar-menu_container a {
    color: #fff;
    font-family: logofont;
    font-weight: 500;
    font-size: 28px;
    line-height: 25px;
    margin: 0 1rem;
    cursor: pointer;
    text-decoration: none;
}

.og__navbar-sign button,
.og__navbar-menu_container button {
    padding: 0.5rem 1rem;
    color: #fff;
    background: var(--color-button);
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
    border: none;
    outline: none;
    cursor: pointer;
    border-radius: 5px;
}

.og__navbar-menu {
    margin-left: 1rem;
    display: none;
    position: relative;
}

.og__navbar-menu svg {
    cursor: pointer;
}

.og__navbar-menu_container {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    
    text-align: end;
    background: var(--color-footer);
    padding: 2rem;
    position: absolute;
    right: 0;
    top: 40px;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0px 0px 5px rgba(0,0, 0,0.2);
}

.og__navbar-menu_container a {
    margin: 1rem 0;
}

.og__navbar-menu_container-links-sign {
    display: none;
}

@media screen and (max-width: 1050px) {

    .og__navbar-menu {
        display: flex;
    }
}

@media screen and (max-width: 2000px) {
    .og__navbar {
      padding: 4rem 15%;
    }
  }

@media screen and (max-width: 1600px) {
    .og__navbar {
        padding: 4rem 10%;
    }
}

@media screen and (max-width: 550px) {
    .og__navbar {
        padding: 4rem 2rem;
    }

    .og__navbar-menu_container {
        top: 20px;
    }

    .og__navbar-menu_container-links-sign {
        display: block;
    } 
}



